<template>
  <v-card color="secondary" flat dark>
    <v-card-title>
      <h1 class="text-h6 white--text">單位資訊</h1>
    </v-card-title>
    <v-card-text class="pt-4 pb-0">
      <v-row>
        <v-col cols="12" md="12" xl="8">
          <v-tabs
            v-model="tab"
            background-color="rgba(255, 255, 255, 0.12)"
            class="my-tabs"
          >
            <v-tabs-slider color="primary"></v-tabs-slider>
            <v-tab v-for="(t, index) in tabs" :key="index">
              <v-icon size="20" class="me-3">
                {{ t.icon }}
              </v-icon>
              {{ t.title }}
            </v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab" class="my-tabs-items">
            <v-tab-item>
              <v-sheet class="d-flex" color="secondary">
                <v-container class="my-container px-6" fluid>
                  <BasicInfo :data="orgData" />
                </v-container>
              </v-sheet>
            </v-tab-item>
            <v-tab-item>
              <v-sheet class="d-flex" color="secondary">
                <v-container class="my-container px-6" fluid>
                  <NumOfPeople />
                </v-container>
              </v-sheet>
            </v-tab-item>
          </v-tabs-items>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import BasicInfo from "@/components/organization/BasicInfo.vue";
import NumOfPeople from "@/components/organization/NumOfPeople.vue";
import { createInvoice } from "@/hooks/api.js";

export default {
  name: "Organization",
  components: {
    BasicInfo,
    NumOfPeople,
  },
  data: () => ({
    tab: null,
    tabs: [
      { title: "基本資料", icon: "mdi-card-account-details-outline" },
      { title: "人數概況", icon: "mdi-account-group" },
    ],
    orgData: {
      name: "無條件科技股份有限公司",
      taxId: "90474963",
      type: 5,
      industry: 15,
    },
  }),
  methods: {
    hideAlert() {
      window.setInterval(() => {
        this.alert = false;
      }, 3000);
    },
    submit() {
      if (this.$refs.form.validate()) {
        createInvoice({
          merchant_trade_no: this.merchant_trade_no,
        })
          .then(() => {
            this.alertType = "success";
            this.alertText = "開立成功";
          })
          .catch((err) => {
            this.alertType = "warning";
            const code = err.response.data.code;
            switch (code) {
              case "09002":
                this.alertText = "此訂單已開立過發票";
                break;
              case "09003":
                this.alertText = "訂單號碼不存在";
                break;
              default:
                this.alertText = "開立失敗";
            }
          });
        this.alert = true;
        this.$refs.form.reset();
        this.hideAlert();
      }
    },
  },
};
</script>

<style scoped>
.my-tabs {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.my-card,
.my-container,
.my-tabs-items {
  background: rgba(140, 140, 140, 0.12) !important;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
}
</style>
